<template>
	<div class="videos">
		<div class="bigbox">
			<div class="swiper-container">
				<div class="swiper-wrapper">
					<div class="swiper-slide" v-for="(item,index) in images" :key="index">
						<img :src="item" alt="">
					</div>
				</div> -->
				<div class="swiper-pagination">
				</div>
			</div>
			<div class="channel">
				<div></div>
			</div>
			<div class="dv">
				<li v-for="(item,index) in items" :key="index" @click="gos(item)">
						<img :src="item.picUrl" alt="">
					<div class="title">{{item.name}}</div>
					<div class="tu">
						<div class="box">
							<span class="playcount">{{Calculate(item.playCount)}}</span>
							<div class="r">
								<img :src="ej" alt="">
							</div>
						</div>
					</div>
					
				</li>
			</div>
			<div class="channel">
				<div></div>
			</div>
			<div class="newmusic">
				<div class="songname">
					<li v-for="(item,index) in musics" :key="index" @click="go(item,musics,index)">
						<div class="theme">
							<span class="tou">{{item.name}}</span>
							<span class="fuben">
								{{item.song.alias[0]}}
							</span>
						</div>
						<div class="bofang">
							<img src="../../public/wyyimg/bofang.png" alt="">
						</div>
						<div class="explain">
							<div class="sq">
								<img src="../../public/wyyimg/sq.png" alt="">
							</div>
							<div class="author">
								<span>{{item.song.artists[0].name}}-{{item.name}}</span>
							</div>
							
						</div>
					</li>
					
				</div>
			</div>
		</div>
		<router-view />
	</div>
</template>
	
<script>
	import '../assets/js/swiper.min.js';
	import axios from 'axios'
	export default {
		name:'videos',
		data() {
			return {
				images:[
						'/wyyimg/lunbo1.png',
						'/wyyimg/lunbo2.png',
						'/wyyimg/lunbo3.png',
						'/wyyimg/lunbo4.png',
						'/wyyimg/lunbo5.png',
						'/wyyimg/lunbo6.png',
						'/wyyimg/lunbo7.png',
						'/wyyimg/lunbo8.png',
						'/wyyimg/lunbo9.png'
				],
				a:'',
				ej:'/wyyimg/erji.png',
				items:null,
				musics:{},
				content:null
			};
		},
		methods: {
			go(item,musics,index) {
				this.$router.push({name:'player',params:{a:item.song.album.picUrl,b:item.id,
				son:musics,index:index}})
			},
			gos(item){
				this.$router.push({name:'song',params:{a:item.name,b:item.picUrl,c:item.playCount,d:item.id}})
			},
			Calculate:function(params){
				if(params > 100000000){
					return (params / 100000000).toFixed(1) + ''
				}
				if(params > 10000){
					return (params / 10000).toFixed(1) + ''
				}
			}
		},
		mounted(){
			new Swiper ('.swiper-container', {
				autoplay:true,
				loop: true,
				duration:3000,// 
				// 
				pagination: {
				  el: '.swiper-pagination',
				},
			})
		},
		created(){
			axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/personalized')
			.then(res => {
				this.items = res.data.result.slice(0,6)
				// console.log(this.items)
			})
			.catch(err => {
				console.log("" + err)
			}),
			axios.get('//api.kele8.cn/agent/http://chiningmeng.top:5555/personalized/newsong')
			.then(res => {
				this.musics = res.data.result
				console.log(this.musics)
			})
			.catch(err => {
				console.log("" + err)
			})
		}
	}
</script>

